import {validatePassword} from "./validatePassword";
import {validateEmail} from "./validateEmail";
import {validateName} from "./validateName";
import {validatePasswordTwo} from "./validatePasswordTwo";
import {validatePhone} from "./validatePhone";

export const validateSingIn = (values) => {
  let errors = {};
  validatePassword(values.password, errors);
  validateEmail(values.email, errors);
  return errors;
};

export const validateCheckoutTwo = (values) => {
  let errors = {};
  validateEmail(values.email, errors);
  validateName(values.name, errors);
  if(!values.region) errors.region = 'Обязательное поле';
  if(!values.address) errors.address = 'Обязательное поле';
  if(!values.citi) errors.citi = 'Обязательное поле';
  validatePhone(values.phone, errors);
  return errors;
};

export const validateSingUp = (values) => {
  let errors = {};
  validateName(values.name, errors);
  validateEmail(values.email, errors);
  validatePasswordTwo(values.password, values.passwordRepeat, errors);
  return errors;
};