import {API} from "../../api/API";
import {setIsNavigation, setNavigation} from "../reducers/shopNav";

export const ThunkGetNavigation = () => {
  return async (dispatch) => {

    const response = await API.getNavigate();
    const navigation = response.data.map(item => {
      return {
        main: {
          name: item.name,
          to: item.id,
        },
        child: item.subcategories.map(item_s => {
          return {
            name: item_s.name,
            to: item_s.id,
          }
        }),
      }
    });

    dispatch(setNavigation([{
      main: {
        name: 'Все товары',
        to: 'all',
      },
      child: []
    },...navigation]));
    dispatch(setIsNavigation(false));
  };
};