import styles from './SearchItem.module.css';
import {NavLink} from "react-router-dom";
import {useDispatch} from "react-redux";
import {setIsActive} from "../../../../redux/reducers/search";
import {useEffect, useState} from "react";

export const SearchItem = ({state, index}) => {

  const dispatch = useDispatch();

  const [showWrapper, setShowWrapper] = useState(true);

  useEffect(() => {
    setTimeout(() => setShowWrapper(false), ((index + 1) * 100))
  }, []);

  return (
    <NavLink
      onClick={() => dispatch(setIsActive(false))}
      to={`/product?id=${state.id}`}
      className={`${styles.wrapper} ${showWrapper && styles.showWrapper}`}
    >
      <div className={styles.photo}>
        <img src={state.photo} alt="state" />
      </div>
      <p className={styles.name}>{state.name}</p>
      <p className={styles.price}>{state.price} Br</p>
    </NavLink>
  );
};