import styles from './ShopNavItem.module.css';
import {useState} from "react";
import {NavLink} from "react-router-dom";

export const ShopNavItem = ({main, child = []}) => {

  const [isShow, setIsShow] = useState(false);
  const [noneChild, setNoneChild] = useState(child.length === 0);

  const onSetIsShow = (boolean) => setIsShow(boolean);

  return (
    <div
      className={styles.wrapper}
    >
      <NavLink
        to={`/?category=${main.to}`}
        className={styles.main}
        onMouseOver={() => !noneChild && onSetIsShow(true)}
        onMouseOut={() => !noneChild && onSetIsShow(false)}
      >
        {main.name}
        <div className={`${styles.childBlock} ${isShow && styles.childBlockShow}`}>
          {child.map(item =>
            <NavLink
              to={`/?subcategory=${item.to}`}
              key={item.to}
              className={styles.childItem}
              onClick={() => setIsShow(false)}
            >{item.name}</NavLink>
          )}
        </div>
      </NavLink>
    </div>
  );
};