export const counterProduct = (counterBasket, context = null) => {
  let number = Number(counterBasket);

  if (context === 'increment') {
    number = number + 1
  } else if (context === 'decrement') {
    number = number - 1
  }

  if (number <= 0) number = 1
  if (counterBasket === '') number = '';
  if (counterBasket >= 99) number = 99;

  return number;
};