import styles from './CheckoutThree.module.css';
import {useDispatch, useSelector} from "react-redux";
import {setCheckoutPage} from "../../../../redux/reducers/basket";
import icon_end from '../../../../image/arrowMenuMobil.png';
import {Thunk_Telegram} from "../../../../redux/thunks/thunk_telegram";
import {Circle} from "../../../../shared/Loaders/circle";

export const CheckoutThree = () => {
  const dispatch = useDispatch();

  const {
    price,
    dataCheckout,
    basketProducts,
    isLoaderSubmit
  } = useSelector(state => state.basket);

  const onSubmit = () => {
    dispatch(Thunk_Telegram({price, dataCheckout, basketProducts}));
  };

  return isLoaderSubmit ? <Circle style={{margin: '0 auto'}}/> : (
    <div className={styles.wrapper}>
      <img
        src={icon_end}
        alt={'icon_end'}
        className={styles.backBtn}
        onClick={() => dispatch(setCheckoutPage(2))}
      />
      <p className={styles.title}>Подтверждение</p>
      <div className={''}>
        <p>Товаров: {basketProducts.length}</p>
        <p>Оплата: {dataCheckout.paymentType}</p>
        <p>Адрес: {`${dataCheckout.region} область, г.${dataCheckout.citi}, ${dataCheckout.address}`}</p>
        <p>Доставка: {dataCheckout.deliveryType}</p>
        <p>Сумма: {price.basketPrice} Br</p><br/>
        <p style={{fontWeight: '700'}}>Итого: {price.deliveryPrice + price.basketPrice} Br</p>
      </div>
      <button
        className={'gold'}
        type="submit"
        onClick={() => onSubmit()}
      >Заказать</button>
    </div>
  );
};