import styles from './MobilMenu.module.css';
import {NavLink} from "react-router-dom";
import {setIsShow} from "../../../../redux/reducers/mobilNav";
import {useDispatch} from "react-redux";
import './stylesActive.css';

export const MobilMenu = () => {

  const dispatch = useDispatch();
  // const isShowMenu = useSelector(state => state.mobilNav.isShow);
  // const {isShow, isShowShop} = useSelector(state => state.mobilNav);

  const renderNavBlock = [
    {to: 'company', name: 'о компании'},
    {to: 'buy', name: 'где купить'},
    {to: 'wholesale', name: 'оптовым партнерам'},
    {to: 'delivery', name: 'доставка'},
    {to: 'contacts', name: 'контакты'}
  ];

  return (
    <div className={styles.wrapper}>
      {renderNavBlock.map(item =>
        <NavLink
          to={item.to}
          key={item.to}
          onClick={() => dispatch(setIsShow(false))}
        >{item.name}</NavLink>)}
    </div>
  );
};