import axios from "axios";
import {telegramForm} from "../../parsing/telegramForm";
import {setCheckoutPage, setIsLoaderSubmit, setReset} from "../reducers/basket";
import {deleteAllBasket} from "../../hooks/useBasket";
import {API} from "../../api/API";

export const Thunk_Telegram = ({price, dataCheckout, basketProducts}) => {
  return async (dispatch) => {

    dispatch(setIsLoaderSubmit(true));
    const TOKEN = '6290813336:AAHl4KmfvWLo52RPawxK7J5kL1n9LqCZdJ0';
    const CHAT_ID = '-929028449';
    const URL_API = `https://api.telegram.org/bot${TOKEN}/sendMessage`;

    const response = await axios.post(URL_API, {
      chat_id: CHAT_ID,
      parse_mode: 'html',
      text: telegramForm({price, dataCheckout, basketProducts})
    });

    if(response.data.ok) {
      deleteAllBasket();
      dispatch(setReset());
      dispatch(setIsLoaderSubmit(false));
      dispatch(setCheckoutPage(4));
    };

  }
};

