import styles from './ShopMainItem.module.css';
import {NavLink} from "react-router-dom";
import {useAddBasket} from "../../../../../hooks/useBasket";
import {useDispatch} from "react-redux";
import {changeBasketLength} from "../../../../../redux/reducers/basket";
import icon_back_photo from "../../../../../image/1_1.png";
import basket_add_icon from '../../../../../image/basket_add.png';
import basket_none_icon from '../../../../../image/basketNone.png';

export const ShopMainItem = ({state}) => {

  const dispatch = useDispatch();
  const [isBasket, addLocalStorageBasket] = useAddBasket(state.id);

  const onAddBasket = () => {
    addLocalStorageBasket(1, state.id);
    dispatch(changeBasketLength());
  };

  return (
    <div className={styles.item}>
      <div
        className={styles.blockPhoto}
      >
        <img
          className={styles.backPhoto}
          src={icon_back_photo}
          alt={icon_back_photo}
        />
        <NavLink
          to={"/product?id=" + state.id}
        >
          <img
            className={`${styles.actionPhoto} `}
            src={"https://45-147-248-20.cloud-xip.com" + state.photo}
            alt={icon_back_photo}
          />
        </NavLink>
        {!isBasket &&
          <div
            onClick={() => !isBasket && state.isStock && onAddBasket()}
            className={`${styles.basketAdd} ${styles.basketAddOn}`}
          >
            {state.isStock ?
              <img
                src={basket_add_icon}
                alt={basket_add_icon}
              />
              :
              <img src={basket_none_icon} alt="basket_none_icon"/>
            }
          </div>
        }
        {isBasket &&
          <NavLink
            to={'/basket'}
            className={`${styles.basketAdd} ${styles.basketAddOn}`}
          >✓</NavLink>
        }
      </div>
      <NavLink to={"/product?id=" + state.id}>
        {state.new && <div className={styles.newBlock}>NEW</div>}
        <p className={styles.name}>{state.name}</p>
        <p className={styles.price}>{state.price} Br</p>
        {!state.isStock && <p className={styles.stock}>нет в наличии</p>}
      </NavLink>
    </div>
  );
};