import styles from './ShopMain.module.css';
import {ShopMainItem} from "./ShopMainItem/ShopMainItem";
import React, {useEffect} from "react";
import {useLocation} from "react-router-dom";
import {Circle} from "../../../../shared/Loaders/circle";
import {ThunkGetCards} from "../../../../redux/thunks/thunk_cards";
import {useDispatch, useSelector} from "react-redux";

export const ShopMain = () => {

  const dispatch = useDispatch();
  const location = useLocation();
  const {cards, isLoader, pageSize} = useSelector(state => state.cards);

  useEffect(() => {
    dispatch(ThunkGetCards({location, pageSize}));
    document.documentElement.scrollTop = 0;
  }, [location]);

  return isLoader ? <Circle /> : (
    <div className={styles.wrapper}>
      <div className={styles.block}>
        {cards.map((item) => <ShopMainItem state={item} key={item.id}/>)}
      </div>
    </div>
  )
}