import './InputText.css';
import {Field} from "formik";
import {ErrorBlock} from "../ErrorBlock/ErrorBlock";

export const InputText = ({name, type, placeholder, errors, touched}) => {
  return (
    <div className='label-input-validate'>
      <Field
        type={type}
        name={name}
        placeholder={placeholder}
      />
      <ErrorBlock errors={errors} touched={touched}/>
    </div>
  );
};