import styles from './InfoQuantity.module.css';
// import icon_love_add from "../../../../../../../image/love_add.png";
import {NavLink} from "react-router-dom";
import {useState} from "react";
import {QuantityBlock} from "./QuantityBlock/QuantityBlock";

export const InfoQuantity = ({state, callBack, isBasket}) => {
  const [counterBasket, setCounterBasket] = useState(1);

  return (
    <>
      <div className={styles.btnBlock}>
        {!isBasket && state.isStock &&
          <>
            <QuantityBlock counterBasket={counterBasket} setCounterBasket={setCounterBasket}/>
            <button
              className={styles.btnAddBasket + ' gold'}
              onClick={() => callBack(counterBasket, state.id)}
            >Добавить в корзину</button>
          </>
        }
        {isBasket && state.isStock &&
          <NavLink
            className={styles.btnAddBasket + ' gold'}
            to={'/basket'}
          >Перейти в корзину</NavLink>
        }
        {!state.isStock &&
          <button
            className={styles.btnIsStock}
            disabled={true}
          >Нет в наличии</button>
        }
      </div>
    </>
  );
};