import {API} from "../../api/API";
import {setCards, setIsLoader} from "../reducers/cards";
import {parsingURL} from "../../parsing/parsingURL";



export const ThunkGetCards = ({location, pageSize}) => {
  return async (dispatch) => {
    dispatch(setIsLoader(true));

    if(parsingURL(location.search).category === 'all' || location.search === '') {
      let response = await API.getCards({});
      dispatch(setCards(response.data.results));
    } else
    if(location.search[1] === 'c') {
      const urlId = parsingURL(location.search).category;
      let response = await API.getCategory({urlId, pageSize});
      dispatch(setCards(response.data));
    } else
    if (location.search[1] === 's') {
      const urlId = parsingURL(location.search).subcategory;
      let response = await API.getSubcategory({urlId, pageSize});
      dispatch(setCards(response.data));
    }

    dispatch(setIsLoader(false));
  };
};