import {createSlice} from "@reduxjs/toolkit";

const initialState = {
  isShow: false,
  isShowShop: false,
};

const MobilNav = createSlice({
  name: 'mobilNav',
  initialState,
  reducers: {
    setIsShow(state, action) {
      state.isShow = action.payload;
      if(action.payload === true) state.isShowShop = false;
    },
    setIsShowShop (state, action) {
      state.isShowShop = action.payload;
    }
  }
});

export const {
  setIsShow,
  setIsShowShop
} = MobilNav.actions;

export default MobilNav.reducer;