export const telegramForm = ({price, dataCheckout, basketProducts}) => {

  const renderOrders = (products) => {
    let text = '';
    products.forEach((item, index) => {
      text = text +
        `--------------------------------------------------\n` +
        `<i>Название:</i> ${item.name}\n` +
        `<i>Цена (шт):</i> ${item.price}Br\n` +
        `<i>Количество (шт):</i> ${item.quantity}\n`
    });
    return text;
  };

  const MESSAGE =
    `<b>Новый Заказ:</b>\n\n` +
    `${renderOrders(basketProducts)}--------------------------------------------------\n\n--------------------------------------------------\n` +
    `<i>Количество товаров:</i> ${basketProducts.length}(шт.)\n` +
    `<i>Тип оплаты:</i> ${dataCheckout.paymentType}\n` +
    `<i>Тип доставки:</i> ${dataCheckout.deliveryType}\n` +
    `<i>Стоимость доставки:</i> ${price.deliveryPrice}Br\n` +
    `<i>Сумма:</i> ${price.basketPrice}Br\n\n` +
    `<b>ИТОГО:</b> ${price.generalPrice}Br\n--------------------------------------------------\n\n` +
    `--------------------------------------------------\n` +
    `<i>Имя:</i> ${dataCheckout?.name}\n` +
    `<i>Адрес:</i> ${dataCheckout.region} область, г.${dataCheckout.citi}, ${dataCheckout.address}\n` +
    `<i>Telegram:</i> @${dataCheckout?.telegram}\n` +
    `<i>Email:</i> ${dataCheckout?.email}\n` +
    `<i>Номер:</i> ${dataCheckout?.photo}\n` +
    `--------------------------------------------------\n\n`;

  return MESSAGE;
}