import {NavLink} from "react-router-dom";
import styles from './MenuNavItem.module.css';
import {useSelector} from "react-redux";
import {useEffect, useState} from "react";

export const MenuNavItem = ({to, icon, cl}) => {
  const num = useSelector(state => state.basket.basketLength);

  const [basketActive, setBasketActive] = useState(false);

  useEffect(() => {
    setBasketActive(true);
    setTimeout(() => setBasketActive(false), 700)
  }, [num]);

  return (
    <NavLink
      to={to}
      className={cl}
    >
      <div className={`${styles.basketActive} ${basketActive && styles.basketActiveOn}`}>
        <img src={icon} alt={icon} />

      </div>
      <div className={styles.quantity}>{num}</div>
    </NavLink>
  );
};