import styles from './Auth.module.css';
import {Navigate, NavLink, useLocation} from "react-router-dom";
import icon_logo from '../../image/logo.png';
import {Login} from "./AuthForms/Login";
import {Registration} from "./AuthForms/Registration";
import icon_back from '../../image/arrowMenuMobil.png';

export const Auth = () => {

  const isAuth = false;
  const authLocation = useLocation().pathname.slice(1) === 'login'; //login : registration

  return isAuth ? <Navigate to='/' /> : (
    <div className={styles.wrapper}>
      <NavLink to='/' className={styles.backPage}>
        <img src={icon_back} alt="icon_back"/>
        Вернуться в магазин
      </NavLink>
      <img src={icon_logo} className={styles.logo}/>
      <div className={styles.block}>
        <p className={styles.name}>{!authLocation ? 'Регистрация' : 'Вход'}</p>
        {authLocation ? <Login /> : <Registration />}
        <div className={styles.bottomBlock}>
          <NavLink
            to={authLocation ? '/registration' : '/login'}
          >
            {!authLocation ? 'Вход' : 'Регистрация'}
          </NavLink>
          {authLocation &&
            <NavLink
              to={'/resetPassword'}
              style={{marginLeft: 'auto', marginRight: '0px'}}
            >Забыли пароль?</NavLink>}
        </div>
      </div>
    </div>
  );
};
