import {Field} from "formik";
import './InputChechbox.css';

export const CheckboxAuth = ({name}) => {
  return (
    <div className='checkbox-auth-block label'>
      <Field
        type='checkbox'
        name={name}
        className='checkbox-auth-item-none'
      />
      <span className='checkbox-auth-span'>
        <div className='checkbox-auth-item-block'>
          <div className='checkbox-auth-item-1-1'></div>
        </div>
        <div className='checkbox-auth-name'>Запомнить меня</div>
      </span>
    </div>
  );
};