import styles from "./ShopMenu.module.css";
import {ShopNavItem} from "./ShopNavItem/ShopNavItem";
import {useSelector} from "react-redux";
export const ShopMenu = () => {

  const category = useSelector(state => state.shopNav.navigation);

  return category === null ? <></> : (
    <menu className={styles.wrapper}>
      <div className={styles.block}>
        {category.map(item => {
          return (
            <ShopNavItem
              key={item.main.name}
              main={item.main}
              child={[...item.child]}
            />
          )
        })}
      </div>
    </menu>
  )
}