import {createSlice} from "@reduxjs/toolkit";

const initialState = {
  isNavigation: false,
  navigation: null,
};

const shopNav = createSlice({
  name: 'shopNav',
  initialState,
  reducers: {
    setIsNavigation(state, action) {
      state.isNavigation = action.payload;
    },
    setNavigation(state, action) {
      state.navigation = action.payload;
    },
  }
});

export const {
  setIsNavigation,
  setNavigation
} = shopNav.actions;

export default shopNav.reducer;


