import styles from './Info.module.css';
import {InfoQuantity} from "./InfoQuantity/InfoQuantity";
import {useAddBasket} from "../../../../../hooks/useBasket";
import {useDispatch} from "react-redux";
import {changeBasketLength} from "../../../../../redux/reducers/basket";

export const Info = ({state}) => {

  const dispatch = useDispatch();
  const [isBasket, addLocalStorageBasket] = useAddBasket(state.id);

  const onAddBasket = (quantity, id) => {
    addLocalStorageBasket(quantity, id);
    dispatch(changeBasketLength());
  };

  return (
    <div className={styles.wrapper}>
      <p className={styles.name}>{state.name}</p>
      <p className={styles.price}>Цена: {state.price} <span>Br</span></p>
      <InfoQuantity
        callBack={onAddBasket}
        state={state}
        isBasket={isBasket}
      />
      <plaintext className={styles.description}>
        <p className={styles.descriptionName}>Описание: </p>
        <p className={styles.descriptionContent}>{state.description}</p>
      </plaintext>
    </div>
  );
};

