import {useEffect, useState} from "react";

export const useShowRender = (closeFunction) => {

  const [isShow, setIsShow] = useState(true);

  useEffect(() => {
    setIsShow(false);
    return () => closeFunction;
  }, []);

  const onClose = (time) => {
    setIsShow(true);
    setTimeout(() => closeFunction(), time);
  };

  return {
    isShow,
    onClose,
  };
};