import styles from './CheckoutTwo.module.css';
import {Form, Formik} from "formik";
import {InputText} from "../../../auth/AuthForms/InputValidate/InputText/InputText";
import {setCheckoutPage, setDataCheckout,} from "../../../../redux/reducers/basket";
import {useDispatch, useSelector} from "react-redux";
import {validateCheckoutTwo} from "../../../auth/AuthForms/Validates/validate";
import icon_end from '../../../../image/arrowMenuMobil.png';
import {InputNumber} from "../../../auth/AuthForms/InputValidate/InputMask/InputMask";

export const CheckoutTwo = () => {

  const dispatch = useDispatch();
  const dataCheckout = useSelector(state => state.basket.dataCheckout);

  const submitSingIn = (values) => {
    dispatch(setDataCheckout(values));
    dispatch(setCheckoutPage(3));
  };

  return (
    <Formik
      initialValues={{
        name: dataCheckout.name,
        telegram: dataCheckout.telegram,
        email: dataCheckout.email,
        region: dataCheckout.region,
        citi: dataCheckout.citi,
        address: dataCheckout.address,
        phone: '',
      }}
      onSubmit={submitSingIn}
      validate={validateCheckoutTwo}
    >
      {({
          errors,
          touched,
          setFieldValue,
        }) => {

        return (
          <Form className={styles.wrapper}>
            <p className={styles.title}>Ваши данные</p>
            <img
              src={icon_end}
              alt={'icon_end'}
              className={styles.backBtn}
              onClick={() => dispatch(setCheckoutPage(1))}
            />
            <div className={styles.block}>
              <InputText
                errors={errors.region}
                touched={touched.region}
                name='region'
                type='text'
                placeholder='Область'
              />
              <InputText
                errors={errors.citi}
                touched={touched.citi}
                name='citi'
                type='text'
                placeholder='Город'
              />
              <InputText
                errors={errors.address}
                touched={touched.address}
                name='address'
                type='text'
                placeholder='Адрес'
              />
              <InputText
                errors={errors.name}
                touched={touched.name}
                name='name'
                type='text'
                placeholder='Имя'
              />
              <InputText
                name='telegram'
                type='text'
                placeholder='Telegram'
              />
              <InputText
                errors={errors.email}
                touched={touched.email}
                name='email'
                type='email'
                placeholder='Email'
              />
              <InputNumber
                errors={errors.phone}
                touched={touched.phone}
                mask="+375 (99) 999-99-99"
                name="phone"
                placeholder='+375 (__) ___-__-__'
                setFieldValue={setFieldValue}
              />
            </div>
            <button
              className={'gold'}
              type="submit"
              disabled={false}
            >Продолжить</button>
          </Form>
        )
      }}
    </Formik>
  );
};
