import styles from './BlockMenuHeader.module.css';
import search_icon from "../../../../image/search.png";
import {NavLink} from "react-router-dom";
import logo_icon from "../../../../image/logo.png";
import basket_icon from "../../../../image/basket.png";
import {NavMobil} from "./NavMobil_Icon/NavMobil";
import {MenuNavItem} from "./MenuNavItem/MenuNavItem";
import {ThemeInput} from "../../../../shared/ThemeInput/ThemeInput";
import {useDispatch} from "react-redux";
import {setIsActive} from "../../../../redux/reducers/search";
// import {ProfileBtn} from "./ProfileBtn/ProfileBtn";
// import love_icon from '../../../../image/love.png';

export const BlockMenuHeader = () => {

  const dispatch = useDispatch();

  return (
    <div className={styles.blockMenu}>
      <button
        onClick={() => dispatch(setIsActive(true))}
        className={styles.searchLeft}
      ><img src={search_icon} alt={search_icon} /></button>
      <NavLink to='/' className={`${styles.logo}`}>
        <img src={logo_icon} alt={logo_icon} />
      </NavLink>
      <div className={styles.menuRightBlock}>
        <button
          onClick={() => dispatch(setIsActive(true))}
          className={styles.searchRight}
        ><img src={search_icon} alt={search_icon} /></button>
        {/*<MenuNavItem*/}
        {/*  to={'/favorites'}*/}
        {/*  icon={love_icon}*/}
        {/*  className={styles.love_icon}*/}
        {/*/>*/}
        <span className={styles.themeMacDisplay}>
          <ThemeInput style={{transform: 'scale(0.7)'}}/>
        </span>
        <MenuNavItem
          to={'/basket'}
          icon={basket_icon}
          cl={styles.basket_icon}
        />
        {/*<ProfileBtn />*/}
        <NavMobil />
      </div>

    </div>
  );
};

