import styles from './Checkout.module.css';
import {useDispatch, useSelector} from "react-redux";
import cross_icon from '../../../image/cross.png';
import {CheckoutOne} from "./CheckoutOne/CheckoutOne";
import {CheckoutTwo} from "./CheckoutTwo/CheckoutTwo";
import {CheckoutThree} from "./CheckoutThree/CheckoutThree";
import {setCheckoutPage, setIsActiveCheckout} from "../../../redux/reducers/basket";
import {useShowRender} from "../../../hooks/useShowRender";
import {CheckoutFour} from "./CheckoutFour/CheckoutFour";
import {useEffect} from "react";

export const Checkout = () => {

  const dispatch = useDispatch();
  const checkoutPage = useSelector(state => state.basket.checkoutPage);
  const {isShow, onClose} = useShowRender(() => dispatch(setIsActiveCheckout(false)));

  useEffect(() => {
    return () => dispatch(setCheckoutPage(1));
  }, [])

  return (
    <div className={`${styles.wrapper} ${isShow && styles.wrapperOff}`}>
      <div className={styles.fon}
           onClick={() => onClose(300)}
      ></div>
      <div className={`${styles.block} ${isShow && styles.blockOff}`}>
        {checkoutPage !== 3 &&
          <img
            src={cross_icon}
            className={styles.crossBtn}
            alt={'cross_icon'}
            onClick={() => onClose(300)}
          />
        }
        {checkoutPage === 1 && <CheckoutOne />}
        {checkoutPage === 2 && <CheckoutTwo />}
        {checkoutPage === 3 && <CheckoutThree />}
        {checkoutPage === 4 && <CheckoutFour />}
      </div>
    </div>
  );
};