import {setIsLoader, setSearchCards} from "../reducers/search";
import {renderCards} from "../../testAPI/render-cards";
import {API} from "../../api/API";

export const ThunkSetSearch = (value) => {
  return async (dispatch) => {
    dispatch(setIsLoader(true));

    console.log(value)
    const response = await API.search(value);
    console.log(response)
    setTimeout(() => {
      dispatch(setSearchCards(value !== '' ? [] : renderCards('all')));
      dispatch(setIsLoader(false));
    }, 500);

  };
};