import {createSlice} from "@reduxjs/toolkit";
import {getLocalBasket} from "../../hooks/useBasket";

const initialState = {
  basketLength: 0,
  basketProducts: [],
  isLoaderBasket: false,
  isLoaderSubmit: false,
  isActiveCheckout: false,
  checkoutPage: 1,
  price: {
    basketPrice: 0,
    deliveryPrice: 0,
    generalPrice: 0,
  },
  dataCheckout: {
    paymentType: '',
    deliveryType: '',
    name: '',
    telegram: '',
    email: '',
    region: '',
    citi: '',
    address: '',
    phone: '',
  },
};

const Basket = createSlice({
  name: 'Basket',
  initialState,
  reducers: {
    setReset(state) {
      state.price = {
        basketPrice: 0,
        deliveryPrice: 0,
        generalPrice: 0,
      };
      state.dataCheckout = {
        paymentType: '',
        deliveryType: '',
        name: '',
        telegram: '',
        email: '',
        region: '',
        citi: '',
        address: '',
      };
      state.basketLength = 0;
      state.basketProducts = [];
    },
    setIsLoaderSubmit (state, action) {
      state.isLoaderSubmit = action.payload;
    },
    setDataCheckout (state, action) {
      state.dataCheckout = {...state.dataCheckout, ...action.payload};
    },
    changeBasketLength (state) {
      state.basketLength = getLocalBasket().length;
    },
    setQuantityBasketProduct(state, action) {
      state.basketProducts.forEach(item => {
        if(item.id === action.payload.id) item.quantity = action.payload.quantity
      });
    },
    setIsLoaderBasket (state, action) {
      state.isLoaderBasket = action.payload;
    },
    setDeliveryPrice (state, action) {
      state.price.deliveryPrice = action.payload;
      state.price.generalPrice = state.price.deliveryPrice + state.price.basketPrice;
    },
    setDeliveryType (state, action) {
      state.dataCheckout.deliveryType = action.payload;
    },
    setPaymentType (state, action) {
      state.dataCheckout.paymentType = action.payload;
    },
    setBasketPrice (state,action) {
      state.price.basketPrice = action.payload;
    },
    setBasketProducts (state, action) {
      state.basketProducts = action.payload;
    },
    removeBasketProduct (state, action) {
      state.basketProducts = state.basketProducts.filter(el => el.id !== action.payload);
    },
    setIsActiveCheckout(state, action) {
      state.isActiveCheckout = action.payload;
    },
    setCheckoutPage(state, action) {
      state.checkoutPage = action.payload;
    },
  }
});

export const {
  setReset,
  setIsLoaderSubmit,
  setDataCheckout,
  setQuantityBasketProduct,
  changeBasketLength,
  setBasketProducts,
  removeBasketProduct,
  setIsLoaderBasket,
  setDeliveryPrice,
  setDeliveryType,
  setPaymentType,
  setBasketPrice,
  setIsActiveCheckout,
  setCheckoutPage
} = Basket.actions;

export default Basket.reducer;

