import styles from './MobilNavShop.module.css';
import {MobilNavItem} from "./MobilNavItem/MobilNavItem";
import {useSelector} from "react-redux";

export const MobilNavShop = () => {

  const category = useSelector(state => state.shopNav.navigation);

  return category === null ? <></> : (
    <nav className={styles.wrapper}>
      {category.map(item => <MobilNavItem state={item} key={item.main.name}/>)}
    </nav>

  );
};