import styles from './NavMobil.module.css';
import {useDispatch} from "react-redux";
import {setIsShow} from "../../../../../redux/reducers/mobilNav";

export const NavMobil = () => {

  const dispatch = useDispatch();

  return (
    <div
      className={styles.wrapper}
      onClick={() => dispatch(setIsShow(true))}
    >
      <div className={`${styles.item} ${styles.item_1}`}></div>
      <div className={`${styles.item} ${styles.item_2}`}></div>
      <div className={`${styles.item} ${styles.item_3}`}></div>
    </div>
  );
};