import React from "react";
import styles from './MobilNav.module.css';
import icon_shopItem from "../../../image/mobilShowIcon.png";
import icon_arrow from '../../../image/arrowMenuMobil.png';
import icon_open from '../../../image/open.png';
import {useDispatch, useSelector} from "react-redux";
import {setIsShow, setIsShowShop} from "../../../redux/reducers/mobilNav";
import {MobilMenu} from "./MobilMenu/MobilMenu";
import {MobilNavShop} from "./MobilNavShop/MobilNavShop";
import {ThemeInput} from "../../../shared/ThemeInput/ThemeInput";
import {AuthButtons} from "../../../shared/AuthButtons/AuthButtons";
import {AuthMenu} from "../../../shared/AuthMenu/AuthMenu";

export const MobilNav = () => {

  const dispatch = useDispatch();
  const {isShow, isShowShop} = useSelector(state => state.mobilNav);
  const isAuth = useSelector(state => state.auth.isAuth);

  return (
    <>
      <div
        className={`${styles.fon} ${isShow && styles.fonOn}`}
        onMouseDown={() => dispatch(setIsShow(false))}
        onTouchStart={() => dispatch(setIsShow(false))}
      ></div>

      <nav className={`${styles.wrapper} ${!isShow && styles.wrapperOff}`}>
        <button
          className={styles.btnBack}
          onClick={() => dispatch(setIsShow(false))}
        ><img src={icon_arrow} alt="icon_arrow"/></button>

        {/*{!isAuth ?*/}
        {/*  <AuthButtons*/}
        {/*    callBack={() => dispatch(setIsShow(!isShow))}*/}
        {/*    cl={styles.wrapperAuthButtonsMobil + ' gold'}*/}
        {/*    gridGap='10px'*/}
        {/*    mr_t='25px'*/}
        {/*    mr_b='30px'*/}
        {/*  />*/}
        {/*  :*/}
        {/*  <AuthMenu*/}
        {/*    callBack={() => dispatch(setIsShow(!isShow))}*/}
        {/*    cl={styles.wrapperAuthMenuMobil}*/}
        {/*    mr_t='30px'*/}
        {/*    gridG='10px'*/}
        {/*    mr_b='30px'*/}
        {/*  />*/}
        {/*}*/}
        <div className={styles.menuBlock}>
          <div
            className={styles.menuBlockShopItem}
            onClick={() => dispatch(setIsShowShop(!isShowShop))}
          >
            <p className={styles.name}>Магазин</p>
            <img
              src={icon_shopItem}
              className={`${styles.menuBlockShopItemOffIcon} ${isShowShop && styles.menuBlockShopItemOnIcon}`}
              alt="icon_shopItem"
            />
          </div>
          {isShowShop ? <MobilNavShop /> : <MobilMenu />}
        </div>
        {!isShowShop &&
          <div className={styles.ThemeInputWrapperMobil}>
            <ThemeInput/>
          </div>
        }
      </nav>
    </>
  );
};


