import {useState} from "react";
const BASKET = 'basket';

export const getLocalBasket = () => JSON.parse(localStorage.getItem(BASKET)) || [];
const checkBasket = (id) => {
  const isBasketLocal = getLocalBasket();
  return isBasketLocal.some(el => el.id === id);
};

export const deleteAllBasket = () => {
  localStorage.setItem(BASKET, JSON.stringify([]));
};

export const useChangeQuantityStorageItem = () => {

  const changeQuantity = (quantity, id) => {
    const data = getLocalBasket();
    data.forEach(item => {
      if(item.id === id) item.quantity = quantity
    });
    localStorage.setItem(BASKET, JSON.stringify(data))
  };
  return [changeQuantity];
};

export const useAddBasket = (id = 1) => {
  const [isBasket, setIsBasket] = useState(checkBasket(id));
  const addLocalStorageBasket = (quantity, id) => {
    const dataLocalStorage = getLocalBasket();
    !checkBasket(id) && localStorage.setItem(BASKET, JSON.stringify([...dataLocalStorage, {id, quantity}]));
    setIsBasket(checkBasket(id));
  };
  return [isBasket, addLocalStorageBasket];
};

export const useRemoveBasket = () => {
  const removeLocalStorageBasket = (id) => {
    const dataLocalStorage = getLocalBasket();
    localStorage.setItem(BASKET, JSON.stringify(dataLocalStorage.filter(el => id !== el.id)));
  };
  return [removeLocalStorageBasket];
};

