import styles from './SearchBlock.module.css';
import {useDispatch, useSelector} from "react-redux";
import {setIsActive} from "../../../redux/reducers/search";
import {useEffect, useState} from "react";
import {SearchItem} from "./SearchItem/SearchItem";
import icon_cross from '../../../image/cross.png';
import {useShowRender} from "../../../hooks/useShowRender";
import {Circle} from "../../../shared/Loaders/circle";
import InputSearch from "./InputSearch";
import {ThunkSetSearch} from "../../../redux/thunks/thunk_search";

export const SearchBlock = () => {

  const dispatch = useDispatch();
  const {isLoader, searchCards} = useSelector(state => state.search);

  const {isShow, onClose} = useShowRender(() => dispatch(setIsActive(false)));

  useEffect(() => {
    dispatch(ThunkSetSearch(''));
  }, [])

  const onSetSearchCards = (value) => {
    dispatch(ThunkSetSearch(value));
  }

  return (
    <div className={`${styles.wrapper} ${!isShow && styles.wrapperBlock}`}>
      <div className={styles.fon} onClick={() => onClose(500)}></div>
      <div className={styles.block}>
        <div className={styles.formBlock}>
          <InputSearch
            callBack={onSetSearchCards}
            placeholder={'Введиту название товара...'}
          />
          <img
            className={styles.cross}
            src={icon_cross}
            alt="icon_cross"
            onClick={() => onClose(500)}
          />
        </div>
        {!isLoader && searchCards.length !== 0 &&
          <p className={styles.info}>
            Найдено {searchCards.length} товара.
          </p>
        }
        <div className={styles.productsWrapper}>
          {
            isLoader ? <Circle style={{margin: '0 auto', transform: 'scale(0.8)'}} />
            :
            <div className={styles.productsBlock}>
              <div className={styles.products}>
                {searchCards.map((item, index) => <SearchItem key={index} index={index} state={item}/>)}
              </div>
            </div>
          }
        </div>
        {!isLoader && searchCards.length === 0 && <p className={styles.searchNone}>Ничего не найдено</p>}
      </div>
    </div>
  );
};