import styles from './BasketItem.module.css';
import {QuantityBlock} from "../../Shop/Product/InfoBlock/InfoQuantity/QuantityBlock/QuantityBlock";
import {useEffect, useState} from "react";
import {useChangeQuantityStorageItem, useRemoveBasket} from "../../../../hooks/useBasket";
import {useDispatch} from "react-redux";
import {
  changeBasketLength,
  removeBasketProduct, setQuantityBasketProduct,
} from "../../../../redux/reducers/basket";
import {NavLink} from "react-router-dom";
import delete_icon from '../../../../image/cross.png';

export const BasketItem = ({state, index}) => {

  const dispatch = useDispatch();

  const [removeLocalStorageBasket] = useRemoveBasket();
  const [changeQuantity] = useChangeQuantityStorageItem();
  const [deleteShow, setDeleteShow] = useState(true);

  const onSetQuantityBasket = (quantity) => {
    dispatch(setQuantityBasketProduct({id: state.id, quantity}));
    changeQuantity(quantity, state.id);
  };

  const onRemoveBasket = (id) => {
    setDeleteShow(true);
    setTimeout(() => {
      removeLocalStorageBasket(id);
      dispatch(removeBasketProduct(id));
      dispatch(changeBasketLength());
    }, 400);
  };

  useEffect(() => {
    setTimeout(() => setDeleteShow(false), (index + 1) * 200 );
  }, []);

  return (
    <div className={`${styles.wrapper} ${deleteShow && styles.wrapperShow}`}>
      <NavLink
        to={`/product?id=${state.id}`}
        className={styles.block_left}
      >
        <div className={styles.photo}>
          <img src={"https://45-147-248-20.cloud-xip.com" + state.photo} alt="state.photo"/>
        </div>
        <p>{state.name}</p>
      </NavLink>
      <div className={styles.block_right}>
        <div className={styles.block_center}><p>{state.price} Br</p></div>
        <QuantityBlock
          counterBasket={state.quantity}
          setCounterBasket={onSetQuantityBasket}
        />
        <div
          className={styles.deleteItem}
          onClick={() => onRemoveBasket(state.id)}
        ><img src={delete_icon} alt="delete_icon"/></div>
      </div>
    </div>
  );
};