import styles from './MobilNavItem.module.css';
import {NavLink} from "react-router-dom";
import {useState} from "react";
import {useDispatch} from "react-redux";
import icon_shopItem from "../../../../../image/mobilShowIcon.png";
import {setIsShow} from "../../../../../redux/reducers/mobilNav";

export const MobilNavItem = ({state}) => {

  const dispatch = useDispatch();
  const [isShowChild, SetIsShowChild] = useState(false);
  const onCloseMenu = () => dispatch(setIsShow(false));

  return (
    <nav className={styles.wrapper}>

      {state.main.to === 'all' ?
        <NavLink
          to={'/?category=all'}
          className={styles.name}
          onClick={() => onCloseMenu()}
        >
          {state.main.name}
        </NavLink>
        :
        <div
          className={styles.name}
          onClick={() => SetIsShowChild(!isShowChild)}
        >
          {state.main.name}
          {
            state.child.length !== 0 &&
            <img
              src={icon_shopItem}
              className={`${styles.nameIcon} ${isShowChild && styles.nameIconOn}`}
              alt="icon_shopItem"
            />
          }
        </div>
      }
      <div className={`${styles.childBlock} ${isShowChild && styles.childBlockOn}`}>
        {
          state.child.map(item =>
            <NavLink
              to={`/?subcategory=${item.to}`}
              key={item.name}
              onClick={() => onCloseMenu()}
            >
              {item.name}
            </NavLink>)
        }
        {
          state.child.length !== 0 &&
          <NavLink
            to={`/?category=${state.main.to}`}
            onClick={() => onCloseMenu()}
          >Показать все</NavLink>
        }
      </div>
    </nav>
  );
};