import {setBasketProducts, setIsLoaderBasket} from "../reducers/basket";
import {API} from "../../api/API";

export const ThunkSetBasketProducts = (basketProductsLocalStorage) => {
  return async (dispatch) => {
    let items = [];

    dispatch(setIsLoaderBasket(true));

    for (let i = 0; i < basketProductsLocalStorage.length; i++) {
      let item =  basketProductsLocalStorage[i];
      const resLoc = await API.getProduct(item.id);
      items.push({...resLoc.data, quantity: item.quantity})
    }

    dispatch(setBasketProducts(items))

    dispatch(setIsLoaderBasket(false));
  };
};