import '../styles/App.css';
import React, {useEffect} from 'react';
import {Auth} from "./auth/Auth";
import {Route, Routes} from "react-router-dom";
import {Main} from "./main/Main";
import {MobilNav} from "./main/MobilNav/MobilNav";
import {useDispatch, useSelector} from "react-redux";
import {useTheme} from "../hooks/useTheme";
import {AuthResetPassword} from "./auth/AuthResetPassword/AuthResetPassword";
import {ThunkGetNavigation} from "../redux/thunks/thunk_auth";
import {changeBasketLength} from "../redux/reducers/basket";

export const App = () => {

  const dispatch = useDispatch();
  const valueTheme = useSelector((state) => state.theme.theme);
  const isShowMenu = useSelector(state => state.mobilNav.isShow);

  const {changeTheme} = useTheme();

  useEffect(() => {
    changeTheme(valueTheme);
  }, [valueTheme]);

  useEffect(() => {
    dispatch(ThunkGetNavigation());
    dispatch(changeBasketLength());
  }, []);

  return (
    <>
      <div className={`App ${isShowMenu && 'wrapperNavOn'}`}>
        <Routes>
          <Route path='/login' element={<Auth />} />
          <Route path='/registration' element={<Auth />} />
          <Route path='/resetPassword' element={<AuthResetPassword />} />
          <Route path='/*' element={<Main />} />
        </Routes>
      </div>
      <MobilNav/>
    </>
  );
};

