import {createSlice} from "@reduxjs/toolkit";

const initialState = {
  cards: [],
  pageSize: 30, //max 100
  pageCurrent: 1,
  pageMax: 0,
  isLoader: true,
  totalCount: null,
  dataScroll: {
    cache: false,
    scroll: null,
    section: null,
  },
};

const Cards = createSlice({
  name: 'Cards',
  initialState,
  reducers: {
    setIsLoader (state, action) {
      state.isLoader = action.payload;
    },
    setCards (state, action) {
      state.cards = action.payload;
    },
  }
});

export const {
  setIsLoader,
  setCards
} = Cards.actions;

export default Cards.reducer;