import styles from './CheckoutOne.module.css';
import {Form, Formik} from "formik";
import {InputRadio} from "../../../auth/AuthForms/InputValidate/InputRadio/InputRadio";
import {useDispatch, useSelector} from "react-redux";
import {setCheckoutPage, setDeliveryPrice, setDeliveryType, setPaymentType} from "../../../../redux/reducers/basket";
import {validateRadio} from "../../../auth/AuthForms/Validates/validateRadio";

const DELIVERY_TYPE_ONE = 'Самовывоз - 0Br';
const DELIVERY_TYPE_TWO = 'Доставка до пункта выдачи - 5Br';
const DELIVERY_TYPE_THREE = 'Доставка до двери - 12Br';

export const CheckoutOne = () => {

  const dispatch = useDispatch();
  const dataCheckout = useSelector(state => state.basket.dataCheckout);

  const onChangeDelivery = (values) => {
    const getDeliveryPrice = (deliveryType) => {
      if(deliveryType === DELIVERY_TYPE_ONE) {return 0} else
      if(deliveryType === DELIVERY_TYPE_TWO) {return 5} else
      if(deliveryType === DELIVERY_TYPE_THREE) {return 12} else
      if(deliveryType === '') {return 0};
    };
    dispatch(setDeliveryPrice(getDeliveryPrice(values.deliveryType)))
  };

  const submitSingIn = (values) => {
    dispatch(setDeliveryType(values.deliveryType));
    dispatch(setPaymentType(values.paymentType));
    dispatch(setCheckoutPage(2));
  };

  return (
    <Formik
      initialValues={{
        paymentType: dataCheckout.paymentType,
        deliveryType: dataCheckout.deliveryType
      }}
      onSubmit={submitSingIn}
      validate={validateRadio}
    >
      {({
          errors,
          touched,
          values,
        }) => {

        onChangeDelivery(values);
        
        return (
          <Form className={styles.wrapper}>
            <p className={styles.title}>Оформление заказа</p>
            <div className={styles.block}>
              <label className={styles.label}>
                <p>Выбрать способ доставки:</p>
                <InputRadio
                  errors={errors.deliveryType}
                  touched={touched.deliveryType}
                  name='deliveryType'
                  title={DELIVERY_TYPE_ONE}
                />
                <InputRadio
                  errors={errors.deliveryType}
                  touched={touched.deliveryType}
                  name='deliveryType'
                  title={DELIVERY_TYPE_TWO}
                />
                <InputRadio
                  errors={errors.deliveryType}
                  touched={touched.deliveryType}
                  name='deliveryType'
                  title={DELIVERY_TYPE_THREE}
                />
              </label>
              <label className={styles.label}>
                <p>Выбрать способ оплаты:</p>
                <InputRadio
                  errors={errors.paymentType}
                  touched={touched.paymentType}
                  name='paymentType'
                  title={'Оплата наличными'}
                />
                <InputRadio
                  errors={errors.paymentType}
                  touched={touched.paymentType}
                  name='paymentType'
                  title={'Оплата картой'}
                />
              </label>
            </div>
            <button
              className={'gold'}
              type="submit"
              disabled={false}
            >Продолжить</button>
          </Form>
        )
      }}
    </Formik>
  )
};

