import {NavLink} from "react-router-dom";
import styles from './CheckoutFour.module.css';
import icon_end from "../../../../image/back.png";
import icon_ok from '../../../../image/ok.png';

export const CheckoutFour = () => {

  return (
    <div className={styles.wrapper}>
      <img className={styles.okIcon} src={icon_ok} alt="icon_ok"/>
      {/*<p>Ваш заказ принят ✓</p>*/}
      <NavLink to={'/'}>
        Вернуться в магазин
        <img src={icon_end} alt="icon_end"/>
      </NavLink>
    </div>
  );
};
