import {createSlice} from "@reduxjs/toolkit";

const initialState = {
  theme: 'light',
};

const Theme = createSlice({
  name: 'Theme',
  initialState,
  reducers: {
    setTheme(state, action) {
      state.theme = action.payload;
    }
  }
});

export const {
  setTheme
} = Theme.actions;

export default Theme.reducer;