import styles from "./Basket.module.css";
import {BasketItem} from "./BasketItem/BasketItem";
import {getLocalBasket} from "../../../hooks/useBasket";
import {useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";
import {NavLink} from "react-router-dom";
import {Checkout} from "../../main/Checkout/Checkout";
import {setBasketPrice, setBasketProducts, setIsActiveCheckout} from "../../../redux/reducers/basket";
import {ThunkSetBasketProducts} from "../../../redux/thunks/thunk_basket";
import {Circle} from "../../../shared/Loaders/circle";
import icon_end from '../../../image/back.png'

export const Basket = () => {

  const dispatch = useDispatch();

  const basketPrice = useSelector(state => state.basket.price.basketPrice);
  const {basketProducts, isActiveCheckout, isLoaderBasket} = useSelector(state => state.basket);

  useEffect(() => {
    dispatch(setBasketProducts([]));
    getLocalBasket().length !== 0 && dispatch(ThunkSetBasketProducts(getLocalBasket()));
    document.documentElement.scrollTop = 0;
  }, []);

  useEffect(() => {
    let priceBasket = 0;
    basketProducts.forEach(el => priceBasket = priceBasket + (el.quantity * el.price));
    dispatch(setBasketPrice(priceBasket));
  }, [basketProducts]);

  return isLoaderBasket ? <Circle /> : (
    <div className={styles.wrapper}>
      {isActiveCheckout && <Checkout />}
      <div className={styles.block}>
        {basketProducts.length !== 0 && basketProducts.map((item, index) =>
          <BasketItem
            key={item.id}
            state={item}
            index={index}
          />
        )}
        {basketProducts.length !== 0 &&
          <div className={styles.basketForm}>
            <p>Количество товаров: {basketProducts.length}</p>
            <p style={{fontWeight: '600'}}>Сумма: {basketPrice} Br</p>
            <button
              className={`gold`}
              onClick={() => dispatch(setIsActiveCheckout(true))}
            >оформить заказ</button>
          </div>
        }
        {basketProducts.length === 0 && <p className={styles.emptyBasket}>
          <p>Корзина пустая</p>
          <NavLink to={'/'}>
            перейти в магазин
            <img src={icon_end} alt="icon_end"/>
          </NavLink>
        </p>}
      </div>
    </div>
  );
};
