import styles from './FormStyle.module.css';
import {Form, Formik} from "formik";
import {InputText} from "./InputValidate/InputText/InputText";
import {InputPassword} from "./InputValidate/InputPassword/InputPassword";
import {CheckboxAuth} from "./InputValidate/InputCheckbox/InputCheckbox";
import {useDispatch} from "react-redux";

export const Login = () => {
  const dispatch = useDispatch();

  const submitSingIn = (values) => {
    values.captcha = '';
  };

  let buttonAuthDisable = false;

  return (
    <Formik
      initialValues={{
        email: '',
        password: '',
        rememberMe: false,
      }}
      onSubmit={submitSingIn}
    >
      {({
          errors,
          touched,
        }) => {

        return (
          <Form className={styles.formBlock}>
            <InputText
              errors={errors.email}
              touched={touched.email}
              name='email'
              type='email'
              placeholder='Email...'
            />
            <InputPassword
              errors={errors.password}
              touched={touched.password}
              name='password'
              type='password'
              placeholder='Пароль...'
            />
            <CheckboxAuth
              name='rememberMe'
            />
            <button
              className={'gold'}
              type="submit"
              disabled={buttonAuthDisable}
            >Войти</button>
          </Form>
        )
      }}
    </Formik>
  );
};