import styles from './FormStyle.module.css';
import {Form, Formik} from "formik";
import {InputText} from "./InputValidate/InputText/InputText";
import {InputPassword} from "./InputValidate/InputPassword/InputPassword";

export const Registration = () => {

  const submitSingIn = (values) => {
    console.log(values)
  };

  let buttonAuthDisable = false;

  return (
    <Formik
      initialValues={{
        email: '',
        password: '',
        password2: '',
      }}
      onSubmit={submitSingIn}
    >
      {({
          errors,
          touched,
        }) => {

        return (
          <Form className={styles.formBlock}>
            <InputText
              errors={errors.email}
              touched={touched.email}
              name='email'
              type='email'
              placeholder='Email...'
            />
            <InputPassword
              errors={errors.password}
              touched={touched.password}
              name='password'
              type='password'
              placeholder='Пароль...'
            />
            <InputPassword
              errors={errors.password}
              touched={touched.password}
              name='password2'
              type='password'
              placeholder='Пароль...'
            />
            <button
              className={'gold'}
              type="submit"
              disabled={buttonAuthDisable}
            >Продолжить</button>
          </Form>
        )
      }}
    </Formik>
  );
};