import React, {useCallback, useEffect} from 'react';
import {useState} from "react";
import debounce from "lodash/debounce";
import styles from "./SearchBlock.module.css";
import search_icon from "../../../image/search.png";
import icon_cross from "../../../image/cross.png";

const InputSearch = ({
    placeholder,
    callBack,
    delay = 500,
    valueInitial
  }) => {

  let [value, setValue] = useState(valueInitial);

  useEffect(() => {
    setValue(valueInitial);
  }, [valueInitial])

  const callBackValue = (text) => callBack(text);

  const callBackDebounce = useCallback(debounce(callBackValue, delay), []);

  const updateValue = (event) => {
    setValue(event.target.value);
    callBackDebounce(event.target.value)
  };

  return (
    <form className={styles.form} onSubmit={(e) => e.preventDefault()}>
      <img
        className={styles.inputIcon}
        src={search_icon}
        alt="search_icon"
      />
      <input
        className={styles.input}
        type={'text'}
        value={value}
        placeholder={placeholder}
        onInput={(event) => updateValue(event)}
        autoFocus={true}
      />
    </form>
  );
};

export default InputSearch;