import instance from "./instance";

export const API = {
  getProduct (id) {
    return instance.get(`product/${id}?format=json`);
  },
  getNavigate () {
    return instance.get(`/product/navigation/?format=json`);
  },
  getCategory ({urlId, pageSize}) {
    return instance.get(`/product/category/${urlId}?format=json`);
  },
  getSubcategory ({urlId, pageSize}) {
    return instance.get(`/product/subcategory/${urlId}?format=json`);
  },
  getCards ({pageSize = 50}) {
    return instance.get(`/product/?format=json&limit=${pageSize}`);
  },
  search(term) {
    return instance.get(`/product/search/${term}`);
  }
};