import styles from "../InfoQuantity.module.css";
import icon_decrement from "../../../../../../../image/decrement.png";
import icon_increment from "../../../../../../../image/increment.png";
import {counterProduct} from "../../../../../../../hooks/counterProduct";

export const QuantityBlock = ({counterBasket, setCounterBasket}) => {

  const setCounterProduct = (counterBasket, context) => setCounterBasket(counterProduct(counterBasket, context));

  return (
    <div className={`${styles.quantityBlock}`}>
      <span onClick={el => setCounterProduct( counterBasket, 'decrement')}>
        <img src={icon_decrement} alt="icon_decrement"/>
      </span>
      <input
        className={styles.quantityInput}
        type='number'
        value={counterBasket}
        onChange={e => setCounterProduct(e.currentTarget.value)}
      />
      <span onClick={el => setCounterProduct(counterBasket, 'increment')}>
        <img src={icon_increment} alt="icon_increment"/>
      </span>
    </div>
  )
}