import styles from './Product.module.css';
import React, {useEffect} from 'react';
import {useLocation, useNavigate} from "react-router-dom";
import {render_product} from "../../../../testAPI/render_product";
import {useState} from "react";
import {Gallery} from "./GalleryBlock/Gallery";
import {Info} from "./InfoBlock/Info";
import {Circle} from "../../../../shared/Loaders/circle";
import {parsingURL} from "../../../../parsing/parsingURL";
import {NotFound} from "../../../../shared/NotFound/NotFound";
import icon_back from '../../../../image/back.png';
import {API} from "../../../../api/API";
import axios from "axios";

export const Product = () => {

  const location = useLocation();
  const BLOCK = React.createRef();
  const navigate = useNavigate();

  const urlId = parsingURL(location.search).id;
  const [isLoader, setIsLoader] = useState(true);
  const [state, setState] = useState(null);


  const getProduct = async  (urlId) => {
    setIsLoader(true);
    let res = await API.getProduct(urlId);
    setState(res.data)
    setIsLoader(false);
  };

  useEffect(() => {
    document.documentElement.scrollTop = 0;
    getProduct(urlId);
  }, [urlId]);

  if(state === undefined) return <NotFound />
  return isLoader ? <Circle /> : (
    <>
      <button
        onClick={() => navigate(-1)}
        className={styles.back}
      >
        <img src={icon_back} alt="icon_back"/>
        <p>Назад</p>
      </button>
      <div className={styles.wrapper} ref={BLOCK}>
        <div className={styles.mainTopBlock}>
          <Gallery state={state} />
          <Info state={state}/>
        </div>
      </div>
    </>
  );
};