import {Header} from "./Header/Header";
import {Pages} from "../pages/Pages";
import {Footer} from "./Footer/Footer";
import React from "react";
import {useSelector} from "react-redux";
import {SearchBlock} from "./SearchBlock/SearchBlock";
import {Circle} from "../../shared/Loaders/circle";
import styles from './Main.module.css';

export const Main = () => {

  const searchIsActive =  useSelector(state => state.search.isActive);
  const isNavigation = useSelector(state => state.shopNav.isNavigation);

  return (
    <div className={styles.wrapper}>
      {isNavigation ?
        <div>
          <Circle style={{
            position: 'fixed',
            left: '50%',
            top: '50%',
            transform: 'translate(-50%, -50%)',
            margin: '0px',
          }}/>
        </div>
        :
        <>
          {/*<AdvertisingBlock />*/}
          <Header />
          <Pages />
          <Footer />
          {searchIsActive && <SearchBlock />}
        </>
      }
    </div>
  );
};