import {Field} from "formik";

export const InputRadio = ({name, title = '0', errors, touched}) => {

  const checkError = () => {
    if(touched && errors !== undefined) {
      return errors
    };
  };

  return (
    <div className={`checkbox-auth-block label ${checkError() !== undefined && 'ErrorTrue'}`}>
      <Field
        type='radio'
        name={name}
        value={title}
        className='checkbox-auth-item-none'
      />
      <span className='checkbox-auth-span'>
        <div className='checkbox-auth-item-block'>
          <div className='checkbox-auth-item-1-1'></div>
        </div>
        <div className='checkbox-auth-name'>{title}</div>
      </span>
    </div>
  );
};