import {useDispatch} from "react-redux";
import {setTheme} from "../redux/reducers/theme";
const DARK_COLOR = '#0b2e43';
const LIGHT_COLOR = '#fff';

export const useTheme = () => {
  const dispatch = useDispatch();

  const changeTheme = (valueTheme) => {
    const localStorageTheme = localStorage.getItem('theme');
    if(localStorageTheme === null) localStorage.setItem('theme', 'light');
    document.documentElement.style.setProperty('--backgroundColor', localStorageTheme === 'dark' ? DARK_COLOR : LIGHT_COLOR);
    dispatch(setTheme(localStorageTheme === 'light' ? 'dark' : 'light'));
  }

  return {changeTheme}
}