import React, {useState} from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { FreeMode, Thumbs, Autoplay} from "swiper";
import "swiper/css";
import "swiper/css/free-mode";
import "swiper/css/thumbs";
import './slide.css';
import styles from './Gallery.module.css';
import icon_1_1 from '../../../../../image/1_1.png';
import icon_productSwiperLittle from '../../../../../image/productSwiperLittle.png';


export const Gallery = ({state}) => {
  const [thumbsSwiper, setThumbsSwiper] = useState(null);

  return (
    <div className={styles.wrapper}>
      <div className={styles.block}>
        <img src={icon_1_1} alt="icon_1_1" className={styles.iconBack}/>
        <Swiper
          spaceBetween={5}
          thumbs={{ swiper: thumbsSwiper }}
          className={styles.wrapperBlock}
          autoplay={{
            delay: 3000,
            disableOnInteraction: true,
          }}
          modules={[FreeMode, Thumbs, Autoplay]}
        >
          {
            state.photos.map((el, index) => (
              <SwiperSlide className={styles.wrapperBlockSlide} key={index}>
                <img src={'https://45-147-248-20.cloud-xip.com' + el.image} alt={'sac'}/>
              </SwiperSlide>
            ))
          }
        </Swiper>
      </div>
      <div className={styles.wrapperLittle}>
        <img src={icon_productSwiperLittle} alt="icon_productSwiperLittle" className={styles.iconBackLittle}/>
        <Swiper
          onSwiper={setThumbsSwiper}
          loop={true}
          spaceBetween={5}
          slidesPerView={4}
          freeMode={true}
          watchSlidesProgress={true}
          modules={[FreeMode, Thumbs]}
          className={styles.wrapperLittleBlock + ' wrapperItemsBlock'}
        >
          {
            state.photos.map((el, index) => (
              <SwiperSlide
                className={`${styles.wrapperItemsBlockSlide}`}
                key={index}>
                <img src={'https://45-147-248-20.cloud-xip.com' + el.image} />
              </SwiperSlide>
            ))
          }
        </Swiper>
      </div>
    </div>
  );
};

